import React from "react"

import './startupAcceleratorSection.css'

const StartupAcceleratorSection = () => {
	return (
		<div className="startup-content" id="startupAccelerator">
			<div className="left-side-content">
				<h1>Accelerator</h1>
				<p>Rob an his co-founder were a part of the 2016 cohort of the propel ICT's Launch Program 12-week Startup Accelerator.</p>
				<p>Propel ICT is Eastern Canada's Startup Accelerator. They run a 12-week intense startup accelerator program for startups at all stages. So much was to be learned from the in-residence entrepreneurs, investors, lawyers and more, but here are some of the highlights...</p>
			</div>
			<div className="right-side-content">
				<p>Some other shit</p>
			</div>
		</div>
	)
};

export default StartupAcceleratorSection


